import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class LivreurService {

  sacADos = Array();
  cakesNumber = 0;

  chercherLaCommande(laCommande) {
    this.sacADos = laCommande;
  }

  livrerLaCommande() {
    return this.sacADos;
  }

  cakeCounts() {
    this.cakesNumber = this.sacADos.length;
    return this.cakesNumber;
  }

  constructor() { }
}
